 -->
<template>
  <div class="role">
    <title-header title="角色定义"></title-header>
    <div class="table-data">
      <iot-table
        :tableHeader="tableHeader"
        :tableData="tableData"
        :pagination="pagination"
        :loading="loading"
        @reloaddata="initData"
      >
        <div slot="col-type" slot-scope="scope"><p>{{scope.row.type === 'HQ' ? '总代' : '代理商'}}</p></div>
        <div slot="col-province" slot-scope="scope"><p>{{scope.row.province || ''}}{{scope.row.city || ''}}{{scope.row.county || ''}}{{scope.row.village || ''}}</p></div>
        <div slot="col-confirm" slot-scope="scope">
          <el-button @click="handleDelete(scope.row.code)" type="text" size="small"><span class="iconfont iconshanchu"></span></el-button>
        </div>
      </iot-table>
    </div>
  </div>
</template>

<script>
import TitleHeader from '@/components/titleHeader'
import iotTable from '@/components/iotTable'
import { findList, del } from '@/api/system'
import msgUtil from '@/utils/msgUtil'
import dateUtil from '@/utils/date_util'

export default {
  name: 'Role',
  components: {
    TitleHeader,
    iotTable
  },
  data() {
    return {
      loading: false,
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      },
      tableData: [],
      tableHeader: [
        { prop: 'name', label: '角色名称' },
        { prop: 'desc', label: '描述' },
        { prop: 'createTime', label: '创建时间' },
        { prop: 'confirm', label: '操作', type: 'slot' }
      ]
    }
  },
  mounted() {
    this.initData()
  },
  methods: {
    handleDelete(id) {
      msgUtil.confirm('确定要删除？', '删除后将无法恢复!', () => {
        del(id).then(resp => {
          if (resp.status === 200) {
            msgUtil.info('删除成功')
            this.listData()
          } else {
            msgUtil.error('删除失败', resp.msg)
          }
        }, error => {
          msgUtil.error('删除失败', msgUtil.findErrorMsg(error))
        })
      })
    },
    async initData() {
      this.loading = true
      try {
        const { data } = await findList()
        this.tableData = data.map(r => {
          return {
            name: r.name,
            code: r.code,
            desc: r.desc,
            createTime: dateUtil.format(new Date(r.create_time * 1000), 'YYYY-MM-dd HH:mm:ss'),
            updateTime: r.update_time
          }
        })
        this.pagination.total = data.length || 0
        this.loading = false
      } catch (error) {
        this.loading = false
        msgUtil.error('查询角色列表失败', msgUtil.findErrorMsg(error))
      }
    },
    handleSearch() {
      if (this.queryFilter.name !== '' || this.queryFilter.code !== '' || this.queryFilter.type !== '') {
        this.initData()
      } else {
        msgUtil.error('查询条件不能为空')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.table-data {
  margin-top: 20px;
}
</style>
